import { FC } from "react";
import BottomProfileLink from "./BottomProfileLink";
import { useSelector } from "react-redux";
import { IState } from "../../reducers";
import { IUsersReducer } from "../../reducers/usersReducers";
import { IRandomNumber } from "../../App";
import { ProfileLink, ProfileBox, ProfileImage } from "./LeftSide_styles";
import { CenterFlex } from "../common/CenterFlex";

export const Profile: FC<IRandomNumber> = ({ id }) => {
  const { usersList } = useSelector<IState, IUsersReducer>((globalState) => ({
    ...globalState.users,
  }));
  const { photosList } = useSelector<IState, IUsersReducer>((globalState) => ({
    ...globalState.users,
  }));
  return (
    <ProfileBox>
      {ProfileInfo(
        photosList[id]?.url,
        usersList[id]?.name,
        "Software Developer"
      )}
      <BottomProfileLink />
    </ProfileBox>
  );
};

const ProfileInfo = (photo: string, name: string, job: string) => (
  <>
    <ProfileImage src={photo} alt="profile" />
    <ProfileLink to="/profile">
      {name}
    </ProfileLink>
    <p>Job title - {job}</p>
  </>
);

export default Profile;
