import React, { FC } from 'react'
import styled from 'styled-components'

export const Wrapper = styled.div`
  margin-right: 1em;
  width: 33vw;
  display: flex;
  justify-content: flex-end;
`
const IconBox = styled.div`
  background-color: #ffffff66;
  margin: 10px;
  border-radius: 50%;
  transition: 0.5s;
  width: 55px;
  height: 55px;
  position: relative;
  display:flex;
  cursor: pointer;
    align-items: center;
    justify-content: center;
  &:hover {
    background-color: unset;
    transition: 0.5s;
  }
  &:first-child::before {
    content: '9';
    color: white;
    font-weight: bold;
    position: absolute;
    background-color: red;
    width: 22px;
    height: 22px;
    top: -5px;
    right: -5px;
    border-radius: 50%;
    display:flex;
    align-items: center;
    justify-content: center;
  }
`
interface IButton {
  source: string
  description: string
}

const RightSideNav: FC = () => (
  <Wrapper>
    <Button source={process.env.PUBLIC_URL + '/Icons/house.svg'} description="home page"/>
    <Button source={process.env.PUBLIC_URL + '/Icons/messages.svg'} description="messages"/>
    <Button source={process.env.PUBLIC_URL + '/Icons/bell.svg'} description="notifications"/>
  </Wrapper>
)

const Button: FC<IButton> = (props) => {
  return (
    <IconBox className="iconBox">
      <img  src={props.source} alt={props.description} />
    </IconBox>
  )
}

export default RightSideNav
